      background-image: linear-gradient(to bottom, #0068eb, #007ff4, #0094f9, #00a7fa, #00b9f9);
}
.navbar-light .collapse.show .nav-item .nav-link {
        -webkit-transition: .4s;
        transition: .4s;
}
}
